* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

#wrapper {
  border: 2px solid #999;
  padding: 15px;


  #main-content {
    border: 1px solid red;
    margin-bottom: 15px;
  }
  #left-sidebar,
  #right-sidebar {
    border: 1px solid #ccc;
    margin-bottom: 15px;
  }

}